<template>
<div class="Trade-record">
  <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col :xs="12" :sm="12" :lg="5">
        <p>{{$t("common.Transaction_Type")}}&nbsp</p>
        <Input placeholder="" style="width: 120px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="5">
        <p>{{$t("common.Contract_No")}}&nbsp</p>
        <Input placeholder="" style="width: 130px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="5">
        <p>{{$t("common.Payer_account")}}&nbsp</p>
        <Input placeholder="" style="width: 130px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="9">
        <p>{{$t("common.Payment_date")}}&nbsp</p>
        <DatePicker type="date" placeholder="" style="width: 110px"></DatePicker> -
        <DatePicker type="date" placeholder="" style="width: 110px"></DatePicker>
        <Button type="primary" class="mgl-10">{{$t("common.Query")}}</Button>
      </Col>
  </Row>
  <Table highlight-row :columns="columns1" :data="historyData" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  <Row type="flex" justify="center" class="code-row-bg page-data">
    <Col span="24">
      <Page :total="dataCount" :page-size="pageSize" @on-change="changepage"></Page>
    </Col>
  </Row>
</div>
</template>

<script>
export default {
    name:'Trade_record',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            pageSize:10,
            historyData: [],
            columns1: [
                {
                    title: this.$t("common.Contract_No"),
                    key: 'contract_number',
                    align:'center'
                },
                {
                    title: this.$t("common.Lending_product_number"),
                    key: 'product_number',
                    align:'center'
                },
                {
                    title: this.$t("common.Payment_order_number"),
                    key: 'order_number',
                    align:'center'
                },
                {
                    title: this.$t("common.Actual_amount"),
                    key: 'amount',
                    align:'center'
                },
                {
                    title: this.$t("common.Payer_account"),
                    key: 'account',
                    align:'center'
                },
                {
                    title: this.$t("common.Payment_date"),
                    key: 'time',
                    align:'center'
                },
                {
                    title: this.$t("common.Trading_state"),
                    key: 'state',
                    align:'center',
                    width:120
                }
            ],
            data1: this.mockTableData1 (),
            Personal_info:null,
            rom:''
        }
    },
    methods:{
      handleListApproveHistory(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      mockTableData1 () {
          let data = [];
          for (let i = 1; i < 101; i++) {
              this.random ()
              data.push({
                  contract_number: 'DG20180312000'+i,
                  product_number: '8371029873109'+i,
                  order_number: '2018031259001024'+i,
                  amount:parseInt(Math.random()*(11-1)+1)+',000.00',
                  account:'60138264020029515'+parseInt(Math.random()*(10-1)+1),
                  time:this.rom >0?'--':this.formatTime(this.getRandomDateBetween()),
                  state:this.rom >0?this.$t("common.Not_received"):this.$t("common.Received")
              })
          }
          return data;
      },
      random (){
        this.rom = parseInt(Math.random()*(3-1)+0)
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("October 10, 2017 11:13:00"),
          b = + new Date();
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      formatDate (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
          let M = date.getMinutes();
          M = M < 10 ? ('0' + M) : M;
          let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + m + '-' + d + ' ' + h + ':' + M + ':' + s;
      },
      formatTime (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          return y + '-' + m + '-' + d;
      },
      getMoble() {
          var prefixArray = new Array("130", "131", "132", "133", "135", "137", "138", "170", "187", "189");
          var i = parseInt(10 * Math.random());
          var prefix = prefixArray[i];

          for (var j = 0; j < 8; j++) {
              prefix = prefix + Math.floor(Math.random() * 10);
          }
          return prefix
      },
      onedata (data,index){
        //console.log(data,index)
        this.Personal_info = data
        //this.sendParams ()
      },
      sendParams () {
          this.$router.push({
              path: '/Userinfo',
              name: 'Userinfo',
              params: {
                  name: 'Personal-info',
                  send_data: this.Personal_info
              }
              /*query: {
                name: 'Personal-info',
                send_data: this.Personal_info
              }*/
          })
       }
    },
    created(){
      this.handleListApproveHistory()
    }
}
</script>

<style lang="css">
.Trade-record{

}
.Trade-record .search_criteria{
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
.Trade-record .search_criteria p{
  height: 30px;
}
.Trade-record .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
  background-color: #fff;
  text-align: center;
}
</style>



